@import "../../styles/main.scss";

.popular-post {
  background-color: $pale-pink;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  @include sectionPadding;
  &__title {
    text-align: left;
    padding-bottom: 1rem;
    font-family: $roboto;
    font-size: 1.5rem;
    color: #fff;
    @include atMedium {
      font-size: 2rem;
    }
  }
}
